.vk-icon,
.ok-icon,
.inst-icon {
  position: relative;
}
.vk-icon::before,
.ok-icon::before,
.inst-icon::before {
  content: '';
  position: absolute;
  width: 40px;
  height: 40px;
  background-size: contain;
}
.vk-icon::before {
  background-image: url('../img/vk-ico.svg');
}
.ok-icon::before {
  background-image: url('../img/ok-ico.svg');
}
.inst-icon::before {
  background-image: url('../img/inst-ico.svg');
}
