}
#policy,
#portfolio {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  box-sizing: border-box;
  width: 90vw;
  max-width: 1200px;
  max-height: 90%;
  margin-top: 50px;
  overflow-y: scroll;
  background-color: wheat;
  z-index: 1000;
}
.close-modal-icon-container {
  position: absolute;
  top: 0;
  right: 0;
  margin-top: 15px;
  margin-right: 15px;
  cursor: pointer;
  width: 30px;
  height: 20px;
}
.close-modal-line-one,
.close-modal-line-two {
  width: 20px;
  height: 3px;
  background-color: #d61517;
  transition: 0.4s;
}
.close-modal-line-one {
  margin: 9px auto;
  transform: rotate(-45deg) translate(-5px, 5px);
}
.close-modal-line-two {
  margin: 8px auto;
  transform: rotate(45deg) translate(-4px, -3px);
}
/* Анимация кнопки закрытия модального окна */
.change .close-modal-line-one {
  transform: rotate(0deg) translate(5px, -5px);
  opacity: 0;
}
.change .close-modal-line-two {
  transform: rotate(0deg) translate(4px, 3px);
  opacity: 0;
}
.modal-overlay {
  /* Подложка окна */
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 999;
  background-color: rgba(0, 0, 0, 0.9);
}
.portfolio-list {
  width: 85%;
}
.portfolio-lead {
  display: block;
  margin-bottom: 50px;
  margin-top: 50px;
  padding: 10px;
  color: #f5deb3;
